.footer {
	padding-bottom: 40px;
	@include r(1000) {
		padding: 30px 0; }
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 15px;
		color: transparentize(white, 0.5);
		@include r(900) {
			align-items: flex-start; }
		@include r(640) {
			flex-direction: column;
			align-items: center;
			text-align: center; } }
	&__left {
		display: flex;
		@include r(900) {
			display: block; }
		@include r(640) {
			display: flex;
			margin-bottom: 23px; }
		.copyright {
			@include r(640) {
				font-size: 13px; } } }
	&__center {
		margin-right: 150px;
		@include r(1280) {
			margin-right: 0; }
		@include r(640) {
			order: -1;
			margin-bottom: 30px; }
		.partner {
			display: flex;
			align-items: center;
			@include r(900) {
				flex-direction: column;
				align-items: center;
				text-align: center; }
			&__text {
				margin-right: 20px;
				@include r(900) {
					margin-right: 0;
					margin-bottom: 5px; }
				@include r(640) {
					font-size: 13px; } }
			&__logo {
				width: 109px; } } }
	.social {
		display: flex;
		align-items: center;
		&__link {
			@include nlt {
				margin-right: 23px; } } } }
