.top {
	padding-top: 70px;
	@include r(1440) {
		padding-top: 30px; }
	@include r(1359) {
		padding-top: 40px; }
	@include r(1199) {
		padding-bottom: 40px; }
	@include r(640) {
		padding-bottom: 0; }
	&__content {
		display: flex;
		justify-content: space-between;
		@include r(640) {
			flex-direction: column;
			align-items: center; } }
	&__logo {
		display: flex;
		align-items: flex-end;
		@include r(640) {
			margin-bottom: 20px; } }
	&__menu {
		display: flex;
		align-items: center; }
	&__item {
		font-family: 'Montserrat', sans-serif;
		font-weight: 500;
		font-size: 20px;
		@include nlt {
			margin-right: 35px; }
		@include r(1199) {
			font-size: 18px; } }
	&__link {
		text-decoration: none;
		color: #FFFFFF; }
	.logo {
		display: flex;
		margin-right: 35px;
		margin-left: -64px;
		align-items: flex-end;
		@include r(1440) {
			margin-left: 0; }
		@include r(1199) {
			margin-right: 25px; }
		@include r(640) {
			margin-right: 15px; }
		&__ico {
			margin-right: 10px;
			position: relative;
			top: -18px;
			@include r(1199) {
				width: 41px;
				margin-right: 8px;
				top: -18px;
				height: auto; } }
		&__name {
			font-family: 'Montserrat Alternates', sans-serif;
			font-weight: bold;
			font-size: 40px;
			@include r(1199) {
				font-size: 29px; } } }
	.text {
		font-family: 'KobzarKS',  sans-serif;
		text-shadow: 0px 0px 17.921px #FF57C6;
		font-size: 55px;
		padding: 0 0 0 4px;
		background: #ff62ca;
		background: -webkit-gradient(linear,left top,right top,from(#ff62ca),to(#ff7975));
		background: linear-gradient(to right,#ff62ca 0%,#ff7975 100%);
		-webkit-background-clip: text;
		background-clip: text;
		-webkit-text-fill-color: transparent;
		@include r(1199) {
			font-size: 42px; }
		@include r(640) {
			font-size: 34px; } }
	&__partner {
		display: flex;
		align-items: center;
		@include r(1000) {
			display: none; } }
	.partner {
		font-size: 13px;
		color: transparentize(white, 0.6);
		line-height: 15px;
		margin-right: 20px; } }
