.form {
	@include r(1199) {
		flex: 1; }
	@include r(900) {
		flex: none;
		width: 47%;
		order: -1; }
	@include r(640) {
		width: 100%;
		order: 0; }
	&__title {
		font-family: 'Montserrat', sans-serif;
		font-weight: 500;
		font-size: 30px;
		margin-bottom: 30px; }
	&__registration {
		width: 290px;
		@include r(1199) {
			width: 100%;
			flex: 1; }
		@include r(900) {
			flex: none; } }
	&__btn {
		max-width: 100%;
		width: 100%;
		margin: 47px auto auto; } }
.input {
	position: relative;
	&:not(:last-of-type) {
		margin-bottom: 33px;
		@include r(768) {
			margin-bottom: 35px; }
		@include r(640) {
			margin-bottom: 24px; } }
	&:hover {
		.input__label {
			color: #FF579E; }
		.input__form {
			color: #FF579E;
			border-bottom: 1px solid #FF579E; } }
	&__form {
		width: 100%;
		box-sizing: border-box;
		background: none;
		border: none;
		border-bottom: 1px solid transparentize(#ffffff, .8);
		font-size: 16px;
		padding: 4px 0;
		outline: none;
		color: #ffffff;
		transition: all .3s;
		@include r(800) {
			font-size: 13px; }
		&:focus,
		&.active {
			+ .input__label {
				top: -19px;
				left: 0;
				font-size: 11px;
				color: transparentize(white, .5); } } }
	&__label {
		font-size: 16px;
		color: #fff;
		position: absolute;
		display: block;
		pointer-events: none;
		top: 2px;
		left: 0;
		cursor: pointer;
		transition: all 0.2s ease;
		@include r(640) {
			font-size: 13px; } } }
.form_error {
	font-size: 12px;
	font-style: italic;
	margin-left: 10px;
	margin-top: 3px;
	color: #ff3500; }
