.wheel {
	display: flex;
	align-items: center;
	justify-content: space-between;
	@include r(900) {
		margin: auto;
		margin-top: 30px; }
	&__info {
		display: none;
		width: 290px;
		margin-right: 60px;
		@include r(1199) {
			flex: 1;
			margin-right: 20px; }
		@include r(1000) {
			margin-right: 0; }
		@include r(900) {
			flex: none;
			width: 43%;
			order: -1; }
		@include r(640) {
			width: 100%;
			order: 0; }
		.left {
			@include r(1199) {
				width: 100%;
				flex: 1; }
			@include r(900) {
				flex: none; } } }
	&__num {
		font-family: 'Montserrat', sans-serif;
		font-weight: 500;
		font-size: 30px;
		margin-bottom: 21px;
		@include r(900) {
			font-size: 26px;
			margin-bottom: 12px; } }
	&__title {
		font-family: 'Montserrat', sans-serif;
		font-size: 55px;
		font-weight: bold;
		background: #ffa800;
		background-image: linear-gradient(135deg,#ffa800 0%,#ff57c6 77%,#10e6d9 100%);
		-webkit-background-clip: text;
		background-clip: text;
		-webkit-text-fill-color: transparent;
		line-height: 49px;
		padding-bottom: 10px;
		@include r(900) {
			font-size: 46px; } }
	&__btn {
		max-width: 100%;
		width: 100%;
		margin-top: 44px;
		font-size: 20px;
		margin-bottom: 31px;
		@include r(900) {
			margin-top: 25px;
			margin-bottom: 20px; }
		&.disabled {
			pointer-events: none;
			background: grey; } }
	&__note {
		font-family: 'Montserrat', sans-serif;
		font-weight: 500;
		font-size: 20px;
		line-height: 35px;
		@include r(900) {
			line-height: 28px; }
		.star {
			font-size: 40px;
			display: block;
			color: #FF4492; } }
	.pie-wrap {
		position: relative;
		@include r(800) {
			margin-left: -15px;
			margin-right: -15px; }
		&:after {
			content: '';
			display: block;
			position: absolute;
			background: #322857 url('../img/svg/star.svg') no-repeat center;
			box-shadow: 0px 0px 45.5245px #FF579E;
			width: 150px;
			height: 150px;
			border-radius: 50%;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			z-index: 2;
			background-size: 84px;
			@include r(800) {
				width: 20%;
				height: 20%;
				background-size: 60%; } }
		&:before {
			content: '';
			display: block;
			position: absolute;
			background: url('../img/svg/check.svg') no-repeat center top;
			width: 395px;
			height: 294px;
			background-size: 72%;
			z-index: 1;
			top: 328px;
			left: -46px;
			transform: translateY(-50%);
			@include r(800) {
				width: 68.58%;
				height: 38%;
				top: 50.1%;
				left: -8%; } } }
	&__pie {
		transform: rotate(22.4deg);
		will-change: transform;
		overflow: hidden; }
	.prizes {
		&__ico {
			transform: rotate(90deg);
			@include r(500) {
				transform: rotate(90deg) scale(0.6); } }
		&__item {
			z-index: 1;
			display: flex;
			align-items: center;
			transform-origin: 0px 283px;
			position: absolute;
			left: 50%;
			transform: rotate(0deg) translateX(-50%);
			writing-mode: tb-rl;
			margin-top: 10px;
			@include r(800) {
				transform-origin: 0 48vw; }
			@include r(500) {
				transform-origin: 0 50vw;
				margin-top: 0; } }
		&__text {
			font-family: 'Montserrat', sans-serif;
			font-weight: 500;
			font-size: 16px;
			text-indent: 0;
			@include r(800) {
				font-size: 3vw; }
			@include r(500) {
				margin-top: -15px; } } } }
.pie {
	width: 580px;
	height: 580px;
	border-radius: 50%;
	overflow: hidden;
	position: relative;
	background: url(../img/svg/wheel.svg) center no-repeat;
	transform: rotate(22.4deg);
	background-size: cover;
	@include r(800) {
		width: 100vw;
		height: 100vw; } }

.sector {
	width: 50%;
	height: 50%;
	position: absolute;
	left: 50%;
	top: 0;
	transform-origin: left bottom; }
