body {
	font-family: 'IBM Plex Sans', sans-serif;
	font-size: 16px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscal;
	text-size-adjust: none;
	-moz-text-size-adjust: none;
	-webkit-text-size-adjust: none;
	-ms-text-size-adjust: none;
	color: white;
	overflow-x: hidden;
	background: url('../img/back.jpg') no-repeat center top;
	background-size: cover;
	height: 100vh;
	display: flex;
	flex-direction: column;
	position: relative;
	min-height: 1000px;
	&:after {
		content: '';
		display: block;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 1423px;
		height: 226px;
		background: url('../img/svg/affhub.svg') no-repeat center top;
		pointer-events: none;
		@include r(1280) {
			display: none; } }
	@include r(1440) {
		min-height: 800px; }
	@include r(1199) {
		min-height: auto; } }
html {
	overflow-x: hidden; }
// Font icons
.fico {
	display: inline-block;
	font-size: 3rem;
	&::before {
		display: block; } }

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill,
select:-webkit-autofill:focus,
textarea:-webkit-autofill:focus,
input:-webkit-autofill:focus {
	transition: background-color 5000s ease-in-out 0s;
	-webkit-text-fill-color: white;
	text-fill-color: white;
	color: white !important; }

textarea:-webkit-autofill:hover,
input:-webkit-autofill:hover,
select:-webkit-autofill:hover {
	-webkit-text-fill-color: #ff579e;
	text-fill-color: #ff579e;
	color: #ff579e !important; }

.btn {
	text-align: center;
	color: white;
	font-size: 22px;
	font-weight: 500;
	padding: 19px;
	display: block;
	max-width: 320px;
	background: #FF579E;
	border-radius: 50px;
	transition: all .3s;
	font-family: 'Montserrat', sans-serif;
	border: none;
	cursor: pointer;
	outline: none;
	@include r(1000) {
		padding: 11px;
		font-size: 20px; } }

.btn:hover {
	background: #FF65A6;
	box-shadow: 0px 2px 30px #BB316B; }

.mr {
	margin-right: 60px;
	@include r(1359) {
		margin-right: 30px; }
	@include r(900) {
		margin-right: 0; } }

.main {
	display: flex;
	align-items: center;
	justify-content: center;
	flex: 1;
	.container {
		@include r(800) {
			width: 100%;
			padding: 0 15px; } }
	@include r(1000) {
		padding-top: 30px; }
	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include r(1199) {
			flex-wrap: wrap; } }
	&__last {
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		height: 0; }
	.prize {
		&__img {
			margin-bottom: 40px;
			opacity: 0;
			@include r(900) {
				width: 120px;
				margin-bottom: 20px; } }
		&__text {
			font-family: 'Montserrat', sans-serif;
			font-weight: 500;
			font-size: 30px;
			margin-bottom: 30px;
			line-height: 45px;
			opacity: 0;
			@include r(900) {
				line-height: 31px;
				font-size: 24px;
				margin-bottom: 10px; } }
		&__desc {
			font-size: 16px;
			line-height: 27px;
			max-width: 500px;
			opacity: 0;
			@include r(900) {
				font-size: 15px;
				line-height: 23px; } }
		&__link {
			font-size: 16px;
			line-height: 27px;
			opacity: 0;
			@include r(900) {
				font-size: 15px;
				line-height: 23px; }
			.link {
				text-decoration: none;
				color: #FF579E;
				&:hover {
					text-decoration: underline; } } } }
	&__info {
		margin-left: 79px;
		@include r(1359) {
			margin-left: 30px; }
		@include r(1199) {
			margin-left: 0;
			width: 100%;
			order: -1; }
		@include r(1000) {
			margin-bottom: 30px; }
		@include r(900) {
			width: 50%; }
		@include r(640) {
			width: 100%;
			order: 0;
			margin-bottom: 0;
			margin-top: 30px; } }

	&__ttl {
		font-family: 'Montserrat', sans-serif;
		font-weight: 500;
		font-size: 20px;
		margin-bottom: 30px;
		@include r(1199) {
			margin-bottom: 15px; } }
	&__desc {
		color: #9993AF;
		font-style: italic;
		line-height: 27px; }
	&__text {
		@include nlt {
			margin-bottom: 30px; }
		@include r(1199) {
			@include nlt {
				margin-bottom: 15px; } } }
	&__link {
		color: #FF579E;
		text-decoration: none;
		margin-top: 30px;
		display: flex;
		align-items: center;
		@include r(1199) {
			margin-top: 15px; }
		svg {
			margin-left: 30px;
			@include r(640) {
				margin-left: 10px; } }
		&:hover {
			text-decoration: underline; } } }

.animate {
	.main__content {
		animation: content 1s forwards;
		animation-delay: 1s; }
	.main__last {
		animation: result 1s forwards;
		animation-delay: 2s; }
	.prize__img {
		animation: img 0.8s forwards;
		animation-delay: 2s; }
	.prize__text {
		animation: text 1s forwards;
		animation-delay: 2.3s; }
	.prize__desc {
		animation: text 1s forwards;
		animation-delay: 2.4s; }
	.prize__link {
		animation: text 1s forwards;
		animation-delay: 2.5s; } }

@keyframes content {
	0% {
		opacity: 1;
		transform: scale(1); }
	100% {
		opacity: 0;
		transform: scale(0.5);
		height: 0; } }

@keyframes result {
	0% {
		opacity: 0;
		height: 0; }
	100% {
		opacity: 1;
		height: 100%; } }

@keyframes img {
	0% {
		opacity: 0;
		transform: scale(0.3); }
	100% {
		opacity: 1;
		transform: scale(1); } }

@keyframes text {
	0% {
		opacity: 0;
		transform: translateY(-10px); }
	100% {
		opacity: 1;
		transform: translateY(0); } }
